*
{
  margin: 0;
  padding: 0;
}

body
{
 background-color: antiquewhite;
}

.app
{
  text-align: center;
  color: blue;
}

.h1
{
  color: green;
}
.app-input input{
  font-size: 25px;
  border-radius: 5px;
  border: 1px solid grey;
  outline: none;
  padding: 10px 5px;
  width: 200px;

}

.app button
{
  background-color: rgb(119,0,255);
  color: white;
  border: none;
  margin-top: 10px;
  padding: 10px  30px;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 30px;
}

.navActive
{
  color: rgb(255, 255, 255);
  font-size: 30px;
  font-weight: bold;
  
}

.nav
{
  background-color: blueviolet;
  padding:10px;
  display: flex;
  justify-content: center;
  margin-top: 5px;
  
}

.nav ul
{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: rgb(0, 0, 0);
  font-weight: bolder;
  cursor: pointer;

}

.selectednav
{
  margin-left: -35px;
}
.nav ul li{
  transition: all 0.5s ease-in-out;
  cursor:pointer;
  font-size: 25px;

}
.nav ul li:hover
{
  transform: scale(1.2);
}